import "../../css/card.css";
import "../../css/navigation.css";
import "../../css/collections.css";

import React from "react";
class SelColCard extends React.Component {
    constructor(props){
        super(props);
        this.handleClick = this.handleClick.bind(this);
    }
    handleClick() {
        this.props.HandleClick(this.props.CollectionID)
    }
    render(){
        let {CollectionID, SelectedCol, Title} = this.props
        return (
            <div className="card-st"
            onClick = {this.handleClick}>
                <div className="inner-card">
                    <img src="/static/images/criticalthinking.png" width="250px" height="160px" 
                        className={CollectionID == SelectedCol ? "card-border" : ""} />
                    {/* <img src="/static/images/.png" width="250px" height="160px" 
                        className={CollectionID == SelectedCol ? "card-border" : ""}/> */}
                </div>
                <div>
                    <div className="">
                        <p className="text-center">{Title}</p>
                    </div>
                </div>
            </div>
        );
    }
};



export default SelColCard;