import { getBaseData } from "../../actions/pageActions";
import { register } from "../../actions/pageActions";
import MoreInfo from "../../components/MoreInfo";

import { connect } from "react-redux";

function mapStateToProps(state) {
  return {
    basedata: state.basedata
  };
}

function mapDispatchToProps(dispatch) {
  return {
    getBaseData: () => dispatch(getBaseData()),
    register: register_info => dispatch(register(register_info)),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(MoreInfo);
