import "../../css/docCard.css";
import "../../css/navigation.css";
import "../../css/collections.css";

import React from "react";
class EditableColCard extends React.Component { 
    constructor(props) {
        super(props);
        this.state = {
            Hover: false,
        }
        // console.log(this.state);
        this.MouseHover = this.MouseHover.bind(this);
        this.MouseOut = this.MouseOut.bind(this);
    }

    MouseHover() {
        this.setState({ Hover: true });
    }
    MouseOut() {
        this.setState({ Hover: false });    
    }

    render() {
        return (
             <div className="top-col-image" onMouseOver={this.MouseHover} onMouseOut={this.MouseOut}>
                <div className="img-panel">
                    { this.props.isEmpty && 
                        <img src="/static/images/doc icon.png" />}
                    { !this.props.isEmpty && 
                        <img src="/static/images/criticalthinking.png" width="100%" height="100%" />}
                    <button className={this.state.Hover ? "btn-edit-image" : ".btn-edit-image display-none"}>Edit Image</button>
                </div>
            </div>
    )}
};

export default EditableColCard;