import React from "react";
import { withRouter } from "react-router-dom";
import GoogleLogoutButton from "../containers/GoogleAuth/GoogleLogoutButtonContainer.js";


import "../css/main.css";
import "../css/navigation.css";

class Topbar extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            open: false
        };
        this.showAccount = this.showAccount.bind(this);
    }

    showAccount = () => {
        this.setState(state => {
          return {
            open: !state.open,
          };
        });
    };
    render() {
        return (
            <nav className="navbar navbar-expand-md navbar-light">
              <a className="navbar-brand" href="/home">
                <img className="co-teach__logo" src="/static/images/CoTeacherLogo.png" alt="logo" />
              </a>
              <button className="navbar-toggler co-teach__btn" type="button" data-toggle="collapse" data-target="#navbarNav"
                aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
              </button>
              <div className="collapse navbar-collapse text-right" id="navbarNav">
                <ul className="navbar-nav ml-auto">
                  <li className="nav-item mx-3">
                    <a className="nav-link smooth-hover" href="/home">My Drive</a>
                  </li>
                  <li className="nav-item mx-3">
                    <a className="nav-link smooth-hover" href="/collection">Collections</a>
                  </li>
                  <li className="nav-item mx-3">
                    <a className="nav-link smooth-hover" href="/community">Community</a>
                  </li>
        
                  
                    {/* <li className="nav-item mx-3">
                        <a className="nav-link smooth-hover" href="community.html">Community</a>
                    </li>
                    <li className="nav-item mx-3">
                        <a className="nav-link smooth-hover" href="planning.html">Planning</a>
                    </li> */}
                                 
        
                  <li className="nav-item mx-3">
                    <a className="nav-link smooth-hover" href="/find">Find</a>
                  </li>
                  <li className="nav-item mx-3">
                    <a className="nav-link" href="#" onClick={this.showAccount}>
                        <img src={localStorage.getItem("goog_avatar_url")} width="24px" height="auto" style={{borderRadius: "50%"}} />
                    </a>
                    {this.state.open && (
                        <div className="dropdown pf-dropdown">
                        <ul>
                            <li>
                                <GoogleLogoutButton history={this.props.history} />
                            </li>
                        </ul>
                        </div>
                    )}
                  </li>
                </ul>
              </div>
            </nav>
          );
    }
  
}

export default withRouter(Topbar);
