import React from "react";
import { withRouter } from "react-router-dom";
import Spinner from 'react-spinner-material';
import ColCard from '../../components/CustomComponents/ColCard';

import "../../css/navigation.css";
import "../../css/collections.css";


class CollectionPage extends React.Component {
    constructor(props){
        super(props);
        // this.removeCol = this.removeCol.bind(this);
    }

    componentDidMount() {
        this.props.getCollectionData();
    }

    removeCol = (col_id) => {
        this.props.searchdata.my_collections.map( (item, i) => {
            if (item.pk === col_id) {
                this.props.removeCollection(col_id);
            }
        })
    }

    render() {
        const my_cards = [];
        this.props.searchdata.my_collections.map( (my_collection, i) => {

            my_cards.push(
                <ColCard collectionId={my_collection.pk}
                    title={my_collection.fields.Title} 
                    thumbnail={"/static/images/criticalthinking.png"}
                    isNew={false}
                    removeCol={this.removeCol}
                    key={i}
                />
            )
        })
        const shared_cards = [];
        this.props.searchdata.shared_collections.map( function(shared_collection, i) {
            shared_cards.push(
                <ColCard collectionId={shared_collection.pk}
                    title={shared_collection.title} 
                    thumbnail={"/static/images/criticalthinking.png"}
                    isNew={false}
                    key={i}
                />
            )
        })

        if (this.props.searchdata.isFetching) {
            return (
                <div className="spinner-padding">
                    <Spinner size={50} spinnerColor={"#333"} spinnerWidth={2} visible={true} />
                </div>
            )
        } else {
            return (
                <div className="container">
                    <div className="top-container">
                        <h4>Resource Collections</h4>
                        <p>All of your resources at your fingertips</p>
                        <div className="row mt-4">
                            <div className="col-md-11">
                                <div id="custom-search-input">
                                    <div className="input-group">
                                        <input type="text" className="  search-query form-control" placeholder="Search Collections" />
                                        <button className="btn" type="button">
                                            <span className="fa fa-search"></span>
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="middle=container">
                        <div className="row mt-3">
                            <div className="col col-md-7 order-1">
                                <p className="text-collections">My Collections:</p>
                            </div>
                            <div className="col col-md-push-1 order-2">
                                <div className="float-right">
                                    <p>Sort:</p>
                                </div>
                            </div>
                            <div className="col col-md-push-4 order-3">
                                <button className="round-btn" onClick={this.removeCol}>Recent</button>
                                <button className="round-btn">Abc</button>
                                <button className="round-btn">Custom</button>
                            </div>
                        </div>
                    </div>
                    <div className="row mt-5">
                        {my_cards}
                        <ColCard isNew={true}/>
                    </div>
                    <div className="middle=container">
                        <div className="row mt-3">
                            <div className="col col-md-7 order-1">
                                <p className="text-collections">Collections Shared with me:</p>
                            </div>
                        </div>
                    </div>
                    <div className="row mt-5">
                        {shared_cards}
                    </div>
                </div>
            );
        }
    }  
}

export default withRouter(CollectionPage);
