const initialState = {
	cordata: {},
	err: null,
    isFetching: false,
    strand: [],
    standard: [],
    standard_set: '',
    web_thumbnail: ''
};

function corDataReducer(state = initialState, action) {
	switch (action.type) {
		case "REQUESTING_CORDATA":
            return { ...state, err: null, isFetching: true };
        case "NORMAL_STATUS":
			return { ...state, err: null, isFetching: false };
		case "RECEIVE_RESPONSE_CORDATA":
			return { ...state, cordata: convertCorData(action.resp), isFetching: false };
		case "RECEIVE_RESPONSE_STDATA":
            return { ...state, strand: convertStrand(action.resp), standard_set: action.resp.standard_set, isFetching: false };
        case "RECEIVE_RESPONSE_STNUMDATA":
            return { ...state, standard: convertStandardNum(action.resp), isFetching: false };
        case "RECEIVE_RESPONSE_WEB_THUMBNAIL":
            return { ...state, web_thumbnail: action.resp.thumbnail_url, isFetching: false };
        // case "REGISTER_SUCCESS":
        //     return { ...state, err: null, isFetching: false };
        // case "REGISTER_FAIL":
        //     return { ...state, err: null, isFetching: false };   
		default:
			return state;
	}
}
function convertCorData(cordata){
    // console.log(cordata);
    var subject_triggerword = [];
    cordata.subject_triggerword.map( val => {
        subject_triggerword.push({ label: val.TriggerWord, id: val.id});    
    });
    var grade_triggerword = [];
    cordata.grade_triggerword.map( val => {
        grade_triggerword.push({ label: val.TriggerWord, id: val.id});    
    });
    var doctype = [];
    cordata.doctype.map( val => {
        doctype.push({ label: val.Type, id: val.id});    
    });
    var general_tags = [];
    cordata.general_tags.map( val => {
        general_tags.push({ label: val.Tag, id: val.Tag});    
    });
    return {
        subject_triggerword: subject_triggerword, 
        grade_triggerword: grade_triggerword, 
        doctype: doctype, 
        general_tags: general_tags, 
        collections: cordata.collections
    };
}

function convertStrand(st){
    var strand = [];
    var origin_strand = eval('(' + st.strand + ')');
    origin_strand.map( val => {
        strand.push({ title: val.Strand, id: val.Strand, selected: false, key: "selected_strand"});    
    });
    return strand;
}
function convertStandardNum(code){
    var standard = [];
    var converted_code = eval("(" + code.code + ")");
    converted_code.map( val => {
        standard.push({ title: val.Standard_Number, id: val.id, selected: false, key: "selected_standard", description: val.Description});    
    });
    return standard;
}
export default corDataReducer;
