import React from "react";

import "../../css/card.css";
import "../../css/navigation.css";
import "../../css/collections.css";


class ColCard extends React.Component { 
    constructor(props) {
        super(props);
    }

    removeCol = () => {
        this.props.removeCol(this.props.collectionId);
    }

    render () {
        const { isNew, collectionId, thumbnail, title } = this.props;
        if (isNew) {
            return (
                <div className="col-md-4 mb-40">
                    <div className="square">
                        <button className="btn btn-plus" type="button">
                            <a href="/new-collection"><span className="fa fa-plus"></span></a>
                        </button>
                    </div>
                    <div>
                        <div className="new-collection">
                            <p className="text-center">Add New Collection</p>
                        </div>
                    </div>
                </div>
            )
        } else {
            return (
                <div className="col-md-4 mb-40">
                    <div className="square">
                        <span className="x-col-btn" onClick={this.removeCol}>&times;</span>
                        <a href={"/collection/"+collectionId} style={{width: "100%"}}>
                            <img src={thumbnail} style={{maxWidth: "100%"}} width="100%" height="200px"/>
                        </a>
                    </div>
                    <div>
                        <div className="new-collection">
                            <p className="text-center">{title}</p>
                        </div>
                    </div>
                </div>
            )
        }
    }
    
};

export default ColCard;