import React from "react";
import { NavLink, withRouter } from "react-router-dom";
import Spinner from 'react-spinner-material';
import Select from 'react-select';

import "../../css/find.css";
import "../../css/navigation.css";

import "react-tabulator/lib/css/bootstrap/tabulator_bootstrap.min.css"; // use Theme(s)

// for React 16.4.x use: import { ReactTabulator }
import { ReactTabulator } from "react-tabulator"; // for React 15.x

var printIcon = function(cell, formatterParams){ //plain text value
    // console.log();
    return "<img src='"+ cell.getRow().getData().iconUrl +"' />";
};
const columns = [
    { title: "", field: "thumbnail", align: "center", formatter:printIcon,  width: '5%',},
    { title: "Documents matching your search:", field: "name", width: '35%' },
    { title: "Contributor", field: "owner", width: '15%' },
    { title: "Type", field: "DocType" , width: '15%' },
    { title: "Standards", field: "standard", width: '15%'  },
    { title: "Tags", field: "tags", width: '15%'  },
    { title: "", field: "iconUrl", width: '15%', visible: false  },
    { title: "", field: "url", width: '15%', visible: false  },
  ];
// Editable Example:
const colorOptions = {
    [""]: "&nbsp;",
    red: "red",
    green: "green",
    yellow: "yellow"
  };
  const editableColumns = [
    {
      title: "Name",
      field: "name",
      width: 150,
      editor: "input",
      headerFilter: "input"
    },
    {
      title: "Age",
      field: "age",
      align: "left",
      formatter: "progress",
      editor: "progress"
    },
    {
      title: "Favourite Color",
      field: "col",
      editor: "autocomplete",
      editorParams: {
        allowEmpty: true,
        showListOnEmpty: true,
        values: colorOptions
      },
      headerFilter: "select",
      headerFilterParams: { values: colorOptions }
    },
    { title: "Date Of Birth", field: "dob", align: "center", editor: "input" },
    {
      title: "Rating",
      field: "rating",
      align: "center",
      formatter: "star",
      editor: true
    },
    {
      title: "Passed?",
      field: "passed",
      align: "center",
      formatter: "tickCross",
      editor: true
    }
  ];
class CommunityPage extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            show_optional: true,
            changed_text: "Show more",
            selected_type: 1,
            selected_subject_triggerword: null,
            selected_grade_triggerword: null,
            selected_strand: null,
            selected_standard: null,
            selected_methods: null,
            selected_general_tags: null,
            file: JSON.parse( localStorage.getItem("file") )
        }
        // console.log(this.state);
        this.rowClick = this.rowClick.bind(this);
    }

    handleChangeType(event) {
        this.setState({ selected_type: event.currentTarget.value });
        console.log(event.currentTarget.value);
    }

    componentDidMount() {
        this.props.getSearchData("", "", "");
    }
    selectSubjectTrigger(newValue, actionMeta) {
        console.log(newValue);
        this.setState({ selected_subject_triggerword: newValue });
        this.props.getStrand(newValue, this.state.selected_grade_triggerword);
    }
    showOptional(){
        console.log(this.state.show_optional);
        if(!this.state.show_optional){
            this.setState({show_optional: !this.state.show_optional});
            this.setState({changed_text: "Show More"});
        } else {
            this.setState({show_optional: !this.state.show_optional});
            this.setState({changed_text: "Show Less"});
        }
    }
    rowClick(e, row){
        console.log(row.getData().url);
        window.open(row.getData().url, "_blank");
    }
    goBack(){
        window.history.back();
        localStorage.setItem("isRegistered", false);
        console.log(localStorage);
    }
    render() {
        console.log("----",this.props.searchdata.searchdata);
        if (this.props.searchdata.isFetching) {
            return (
                <div className="spinner-padding">
                    <Spinner size={50} spinnerColor={"#333"} spinnerWidth={2} visible={true} />
                </div>
            )
        } else {
            const options = {
                height: "500px",
                movableRows: true
              };
            return (
                <div className="container text-center" style={{ 'display': 'block'}}>
                    <h2>Find Resources</h2>
                    <p style={{ fontSize: '18px'}}>Search for content relevant to you and your students</p>
                
                    
                    <div className="input-group col-md-12">
                    <input type="text" className="  search-query form-control" placeholder="Search for resources" />
                    <button className="btn" type="button">
                        <span className="fas fa-search"></span>
                    </button>
                    </div>
                    <ReactTabulator
                        ref={ref => (this.ref = ref)}
                        columns={columns}
                        data={this.props.searchdata.searchdata}
                        rowClick={this.rowClick}
                        options={options}
                        data-custom-attr="test-custom-attribute"
                        className="custom-css-class"
                        />
                </div>

            );
        }
    }  
}

export default withRouter(CommunityPage);
