import { getCollectionData, removeCollection, getStrand } from "../../actions/pageActions";
import CollectionPage from "../../components/CollectionPage";

import { connect } from "react-redux";

function mapStateToProps(state) {
  return {
    searchdata: state.searchdata
  };
}

function mapDispatchToProps(dispatch) {
  return {
    getCollectionData: () => dispatch( getCollectionData() ),
    removeCollection: (col_id) => dispatch( removeCollection(col_id) ),
    getStrand: (selected_subject_triggerword, selected_grade_triggerword) => 
        dispatch( getStrand(selected_subject_triggerword, selected_grade_triggerword) ),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(CollectionPage);